.selectedImg {
    border: 2px solid red;
}

.product-title {
    text-transform: capitalize;
    margin-bottom: 15px;
}
.product-info  {
    font-size: 14px;
}
.product-info span {
    font-style: italic;
     color: #FF0076;
     font-weight: bold;
    font-size: 16px;

}
.product-cart {
    background: white;
}
.product-cart a {
    width: 100%;
    display: block;
    padding: 10px;
    text-align: center;
    box-shadow: 1px 1px 1px 1px black ;
    color: #FF0076;

}
.product-cart a.buyBtn {
   
    box-shadow: none ;
    

}

.chipLinks {
    color: white !important;
    background-color: #FF0076 !important;
    border-radius: 23px !important;
}

.buyBtn {
    cursor: pointer;
  text-decoration: none;
  color: red;
  background-color: transparent;
  padding: 1px auto !important;
  width: 100%;
  text-align: center;
  border-radius: 6px;
  text-align: center;
  margin-top:16px;
  border: 1px solid red !important;
  display:block;
outline: none;
 
}
.buyBtn:hover {
    background: #ff0076;
    color: white;
  }